@import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:wght@500;700;900&display=swap");

body {
  margin: 0;
  padding: 0;
  background-color: #131313;
  font-family: "DM Sans", sans-serif;
  color: aliceblue;
}
.title {
  color: #68b984;
}

.connect {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.description {
  font-size: 4rem;
  margin: 0;
}

.hero-img {
  border-radius: 12px;
}
h1 {
  margin-left: 12px;
}

button {
  padding: 4px;
  font-family: "Noto Sans", sans-serif;
  font-weight: 900;
  border-radius: 2px;
  outline: none;
  border: none;
}

.connectBTN {
  cursor: pointer;
  height: 40px;
  margin-right: 12px;
  font-size: 1.3rem;
  padding: 0 12px;
}
.accountBtn {
  margin-right: 12px;
  font-size: 1.3rem;
  padding: 0 12px;
  height: 40px;
}

.landing-page {
  margin-top: 40px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.number {
  font-size: 1.5rem;
  margin-bottom: 4rem;
}
.buyBtn {
  font-size: 1.5rem;
  padding: 12px;
  margin-right: 40px;
}

.buyBtn:hover {
  background-color: rgb(212, 212, 212);
}

.withdrawBtn {
  font-size: 1.5rem;
  padding: 12px;
  margin-right: 40px;
}

span {
  /* color: #ceedc7; */
  color: #68b984;
}
